<template>
  <div class="authorinfo">
    <div class="authorsummay">
      <div class="topbar">作者</div>
      <div class="user">
        <router-link :to="{name:'user_info',params:{
          name:userInfo.loginname
        }}">
          <img :src="userInfo.avatar_url" alt>
          <span>{{userInfo.loginname}}</span>
        </router-link>
      </div>
      <p>积分: {{userInfo.score}}</p>
    </div>
    <div class="recent_topics">
      <p class="topbar">最近创建的主题</p>
      <ul>
        <li v-for="topic in topicMax" :key="topic.id">
          <router-link
            :to="{name:'article_content',params:{
            id:topic.id,
            name:topic.author.loginname
          }}"
          >{{topic.title}}</router-link>
        </li>
      </ul>
    </div>
    <div></div>
  </div>
</template>


<script>
export default {
  name: "SlideBar",
  data() {
    return {
      userInfo: {}
    };
  },
  methods: {
    getuser() {
      this.$http
        .get(`https://cnodejs.org/api/v1/user/${this.$route.params.name}`)
        .then(result => {
          if (result.data.success == true) {
            this.userInfo = result.data.data;
          }
        })
        .catch(err => {
          console.log(err);
        });
    }
  },
  beforeMount() {
    this.getuser();
  },
  computed: {
    topicMax() {
      if (this.userInfo.recent_topics) {
        return this.userInfo.recent_topics.slice(0, 4);
      }
    }
  }
};
</script>

<style scoped>
.authorsummay,
.recent_replies,
.recent_topics {
  background-color: #fff;
}
.authorinfo {
  width: 328px;
  float: right;
  margin-top: 0;
}
li {
  padding: 3px 0;
}
.recent_replies ul,
.recent_topics ul {
  margin-top: 0px;
  margin-bottom: 0px;
  list-style: none;
  padding-left: 14px;
}

ul a {
  font-size: 12px;
  margin-left: 8px;
  text-decoration: none;
  color: #778087;
}

.authorsummay .topbar {
  margin-top: 0px;
}

.topbar {
  padding: 10px;
  background-color: #f6f6f6;
  height: 16px;
  font-size: 12px;
  margin-top: 10px;
}

img {
  height: 48px;
  width: 48px;
  border-radius: 3px;
  margin: 10px;
}

.user a{
  text-decoration: none;
  color: #778087
}

.user a img,
.user span
{
  vertical-align:middle
}

.authorsummay p {
  padding-left: 10px;
  font-size: 14px;
  font-weight: 400;
  padding-bottom: 10px;
}
</style>
